<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="ManageCategory"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
            IdentityColumn="TICKET_ARTICLE_CATEGORY_ID" :HeaderText="$t('ManageCategory')" :ListData="SAData"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'NoOfArticle'">
                 
                    <td class="text-center single-action">
                        <span><a class="fa fa-file-text-o" style="font-size:20px !important;color:black !important;" aria-hidden="true"
                                @click="openViewDetail(data.row.TICKET_ARTICLE_CATEGORY_ID)"></a><span
                                class="noti-circle noti-green">{{
                                    data.row.NoOfArticle }}</span></span>
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <a class="inactive-disabled">

                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Edit'">
                    <td class="text-center single-action">
                        <a @click="AddCategory(data.row.TICKET_ARTICLE_CATEGORY_ID)" title="Edit"
                            class="updateCat editicon ">
                            <em class="fa fa-pencil text-success action-icon"></em>
                        </a>
                    </td>
                </template>
            </template>
        </tg-list>
        <AddCategory v-if="IsCategory" :CategoryId="CategoryId"></AddCategory>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isArticleDetail">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('ArticleList') }}</h4>
                                <button type="button" class="close" v-on:click="popupClose">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <div class="col-md-12">
                                    <div class="listing">
                                        <div class="table-responsive table-fix-header">
                                            <table class="table table-bordered dt-responsive nowrap mt-0" id="tbl_formList">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th class="sorting"><span>{{ $t('Article') }}</span></th>
                                                        <th><span>{{ $t('Status') }}</span></th>
                                                        <th class="text-center"><span>{{ $t('ShortDescription') }}</span>
                                                        </th>
                                                        <th class="text-center sorting"><span>{{ $t('View') }}</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(row, index) in ArticleList">
                                                        <td>
                                                            <span>{{ row.TICKET_ARTICLE_TITLE }}</span>
                                                        </td>
                                                        <td>
                                                            <span
                                                                v-bind:class="getStatusClassList(row.STATUS_NAME, 'STATUS_NAME')">
                                                                {{ row.STATUS_NAME }}
                                                            </span>
                                                        </td>
                                                        <td>
                                                            <span>{{ row.TICKET_ARTICLE_KEYWORDS }}</span>
                                                        </td>
                                                        <td class="single-action text-center" style="font-size: 20px;">
                                                            <a @click="ClickOnView(row.TICKET_ARTICLE_ID)">
                                                                <i class="fa fa-eye text-info action-icon"></i>
                                                            </a>
                                                        </td>
                                                    </tr>
                                                    <tr v-if="(ArticleList == null || ArticleList.length == 0)">
                                                        <td colspan="4" class="text-danger text-center">
                                                            {{ $t('NoRecordfound') }} </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>
<script>
import Schema from './ManageCategorySchema'
import DataServices from "@/services/DataService";
import AddCategory from './AddCatgory.vue'
export default {
    components: { AddCategory },
    data() {
        return {
            isLoading: false,
            noRecords: false,
            isArticleDetail: false,
            CategoryId: null,
            IsAddCannedReply: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            Headers: [{
                "COLUMN_NAME": "TICKET_ARTICLE_CATEGORY_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Category Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "SUBCATEGORYNAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Sub Category",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "NoOfArticle",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Number Of Articles",
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Edit",
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            }
            ],
            SAData: [],
            ArticleList: [],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            searchCondition: '',
            TemplateId: null,
            IsCategory: false
        }
    },
    created: async function () {
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Category");
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Sub Category");
        this.FetchData();
    },
    methods: {

        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            ////debugger;
            var url = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&isPaged=1&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&${vm.searchCondition}`;
            DataServices.ManageCategoryList(url).then(response => {
                if (response.data.status == 'Success') {
                    ////debugger;
                    if (response.data.result.data.length > 0) {
                        vm.SAData = response.data.result.data;
                        vm.TotalRecords = response.data.result.data[0].TOTAL_RECORDS;
                        vm.listheaderbuttons[1].isdisabled = true;
                        vm.isLoading = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.SAData = [];
                        vm.isLoading = false;
                    }
                } else {
                    vm.noRecord = true;
                    vm.TotalRecords = 0;
                    vm.SAData = [];
                    vm.isLoading = false;
                }
            })
        },
        ClickOnView: function (ths) {
            //debugger
            this.$router.push({ name: 'ArticleDetails', params: { id: ths } });
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Published':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;


                default:
                    returnClass = '';
            }
            return returnClass;
        },
        openViewDetail: function (categoryId) {
            var vm = this;
            //debugger;
            var vm = this;
            vm.isLoading = true;
            //debugger;
            var url = `Category=${categoryId}&SortBy=${vm.SortBy}&SortExp=${vm.SortExp}&PageSize=${vm.PageSize}&PageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            DataServices.GetArticleListing(url).then(response => {
                if (response.data.status == 'Success') {
                    vm.isLoading = false
                    if (response.data.result.length > 0) {
                        vm.ArticleList = response.data.result;
                    } else {
                        vm.ArticleList = [];
                    }
                }
            })
            vm.isArticleDetail = true;
        },
        popupClose: function () {
            var vm = this;
            vm.isArticleDetail = false;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case 'callAdd':
                    this.AddCategory(0);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        AddCategory: function (value) {
            this.CategoryId = value;
            this.IsCategory = true;
        },
        renderActions: function () { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function () { },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Category")), function () {
                    ////debugger;
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "KnowledgeBase",
                        refCode: "TALYGEN_ticket_article_category"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Category')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Category')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        // commonDeleteData: function (event) {            
        //     var vm = this;
        //     var val = "";
        //     $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
        //         if (val.length > 0)
        //             val += ',';
        //         val += $(this).val();
        //     });
        //     if (val.length > 0) {
        //         vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Category")), function () {
        //             var objectPostString = `ids=${val}&tableName=${'TALYGEN_ticket_article_category'}&primaryKey=${'ticket_article_category_id'}`;   
        //             DataServices.DeleteData(objectPostString).then(response => {
        //                 var box = "";
        //                 if (response.data == "DeletedSuccess") { 
        //                     vm.listheaderbuttons[1].isdisabled = true;                                                   
        //                     vm.FetchData();
        //                     box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Category')]), "Success", true, vm.$t('Alert'));
        //                 }
        //                 else {
        //                     box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Category')]), "failure", true, vm.$t('Alert'));
        //                 }
        //                 setTimeout(function () {
        //                     box.modal('hide');
        //                 }, 2000);
        //             });
        //         });
        //     }
        //     else {
        //         alert(vm.$t("Selectanyrecordtodelete"));
        //     }
        // },

    }
}
</script>
<style>
.textstyle {
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
}
</style>